import ajax from './ajax'
// const BASE_URL = 'http://localhost:4000'
const BASE_URL = '/api'

// 1
export const reqAddress = (geohash) => ajax(`${BASE_URL}/position/${geohash}`)
// 2
export const reqFoodCategorys = () => ajax(BASE_URL + '/index_category')
// 3
export const reqShopsLists = (longitude, latitude) => ajax(BASE_URL + '/shops', {longitude, latitude})
// 4
export const reqSearchShopsLists = (geohash, keyword) => ajax(BASE_URL + '/search_shops', {geohash, keyword})
// 6
export const reqNameLogin = ({name, pwd, captcha}) => ajax(BASE_URL + '/login_pwd', {name, pwd, captcha}, 'POST')
// 7
export const reqShortMsg = (phone) => ajax(BASE_URL + '/sendcode', {phone})
// 8
export const reqPhoneLogin = (phone, code) => ajax(BASE_URL + '/login_sms', {phone, code}, 'POST')
// 9
export const reqUserInfo = () => ajax(BASE_URL + '/userinfo')
// 10
export const reqLoginOut = () => ajax(BASE_URL + '/logout')

// 
export const reqShopInfo = () => ajax('./info')
// 
export const reqShopRating = () => ajax('./ratings')
// 
export const reqShopGoods = () => ajax('./goods')



